.Loader {
  width: 100%;
  height: 100%;

  position: fixed;
  top: 0;

  background-color: #2c3e50;
}

.Spinner {
  width: 40px;
  height: 40px;

  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.Spinner__One,
.Spinner__Two {
  width: 100%;
  height: 100%;

  position: absolute;
  top: 0;
  left: 0;

  border-radius: 50%;
  background-color: #fff;

  opacity: 0.6;

  -webkit-animation: bounce 2s infinite ease-in-out;

          animation: bounce 2s infinite ease-in-out;
}

.Spinner__Two {
  -webkit-animation-delay: -1s;
          animation-delay: -1s;
}

@-webkit-keyframes bounce {
  0%,
  100% {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  50% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes bounce {
  0%,
  100% {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  50% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}



